const getInstance = () => {
  const node = new window.Ipfs({
    EXPERIMENTAL: {
      pubsub: true,
    },
    config: {
      Addresses: {
        Swarm: [
          '/dns4/ws-star.discovery.libp2p.io/tcp/443/wss/p2p-websocket-star',
        ],
      },
    },
  });

  return new Promise(resolve => {
    node.once('ready', () => resolve(node));
  });
};

export default {
  getInstance,
};
